@import url("https://fonts.googleapis.com/css2?family=Fredoka+One&family=Fugaz+One&family=Permanent+Marker&display=swap");

.main {
  display: flex;
  justify-content: center;
  align-items: center;
  /* to vertically centre */
  height: 500px;
}
main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 50vh;
  background-color: lightblue;
  border-radius: 5px;
  background-color: #e9d8e4;
  color: #343f71;
  font-family: "Fredoka One", cursive;
}

main h1 {
  color: #d75788;
}

.start-button {
  margin-top: 15px;
  width: 100px;
  height: 27px;
  background-color: #e8eaf9;
  font-family: "Fredoka One", cursive;
  color: #d75788;
  border: solid 1px rgb(215, 204, 249);
  box-shadow: 1px 1px 5px rgb(206, 206, 240);
  font-size: large;
  cursor: pointer;
  border-radius: 3px;
}
